@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{box-sizing:border-box}html{font-size:62.5%;scroll-behavior:smooth}@media screen and (max-width: 700px){html{font-size:50%}}body{width:100%;font-family:"Quicksand";padding:1rem 8rem;margin:0}@media screen and (max-width: 700px){body{padding:1rem 2rem}}.main-page{font-size:1.6rem}.main-page__teams{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.main-page table{border-collapse:collapse;margin:2rem 0;width:90%;border-radius:5px 5px 0 0;box-shadow:0 0 7px #000}.main-page table thead tr{background-color:#25aefe;font-size:2rem;font-weight:bold;color:#f3f3f3}.main-page table td,.main-page table th{padding:2rem}@media screen and (max-width: 1000px){.main-page table td,.main-page table th{padding:.5rem .5rem}}.main-page table tbody tr{border-bottom:1px solid gray;transition:all .3s;cursor:pointer}.main-page table tbody tr:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.main-page table tbody tr:nth-child(even){background-color:#e6e4e4}.main-page table tbody tr:nth-child(odd){background-color:#f3f3f3}.main-page table tbody tr:last-of-type{border-bottom:4px solid #25aefe}th{text-align:left}.player img{width:60px;height:60px;border-radius:50%;border:4px solid #000}@media screen and (max-width: 700px){.player img{width:25px;height:25px}}.team{padding:.5rem;font-size:1.6rem;margin:1rem;text-align:center;cursor:pointer;border-radius:5px;transition:all .4s}@media screen and (max-width: 700px){.team{font-size:1.2rem;margin:.5rem}}.team:hover{background-color:#25aefe;color:#f3f3f3}.team__selector{height:3rem;background-color:#f3f3f3;transition:all .4s}@media screen and (max-width: 700px){.team__selector{height:2rem}}.team img{width:60px;height:60px}@media screen and (max-width: 700px){.team img{width:25px;height:25px}}.team p{font-size:1.4rem;min-width:15rem}@media screen and (max-width: 700px){.team p{min-width:13rem}}
